import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';


class ModuleModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Module
                        </Modal.Title>
                    </Modal.Header>

                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="ModuleName">
                                <Form.Label>Module Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter Module Name"
                                    value={this.props.MODULE_NAME}
                                    onChange={this.props.changeEventandValidation} name="ModuleName" />
                                       <div className="invalid-feedback d-block">
                                    {this.props.ErrorModuleName}
                                </div>
                            </Form.Group>
                            <Form.Group controlId="DEPARTMENT_NAME">
                                <Form.Label>Department Name</Form.Label>
                                <InputGroup>
                                    <Form.Control value={this.props.DEPARTMENT_ID}
                                        onChange={this.props.changeEventandValidation} hidden name="DepartmentID" />
                                    <Form.Control
                                        type="text"
                                        value={this.props.DEPARTMENT_NAME}
                                        // onChange={this.props.handleChangeDepartmentName}
                                        onChange={this.props.changeEventandValidation}
                                        placeholder="Search Department Name"
                                        aria-describedby="inputGroupPrepend"
                                        readOnly
                                        name="DepartmentName"

                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleDepartmentSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                </InputGroup>
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorDepartmentName}
                                </div>
                            </Form.Group>
                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>System Status Type</Form.Label>
                                <InputGroup>
                                    <Form.Control value={this.props.SystemStatusTypeID}
                                        onChange={this.props.changeEventandValidation} hidden name="StatusID" />
                                    <Form.Control
                                        type="text"
                                        value={this.props.SystemStatusTypeDescription}
                                        // onChange={this.props.handleChangeSystemStatusTypeDescription}
                                        placeholder="Search System Status Type"
                                        aria-describedby="inputGroupPrepend"
                                        //required
                                        onChange={this.props.changeEventandValidation}
                                        readOnly
                                        name="Status"
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                </InputGroup>
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorStatus}
                                </div>
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Module
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button"
                                 value={this.props.MODULE_NAME} onChange={this.props.MODULE_NAME} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default ModuleModal;